.container {
    font-family: 'Pretendard-Regular', Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans";
    height: 100%;
    display: flex;
    justify-content: center;
  }
  
.loader {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}
.movies {
    display: grid;
    grid-template-columns: repeat(2, minmax(400px, 1fr));
    /* gird-gap: 100px; 적용되지 않아서 gap으로 변경*/
    gap: 100px;
    padding: 50px;
    width: 80%;
    padding-top: 70px;
}

@media screen and (max-width: 1090px) {
    .movies {
      grid-template-columns: 1fr;
      width: 100%;
    }
}
  